export async function agreementText({
  agreement,
  setAgreement,
  isLocal = true,
  header,
}) {
  setAgreement({
    ...agreement,
    loading: true,
  });

  try {
    if (isLocal) {
      const url = `/${config.theme}/assets/agreement.txt`;
      const { data } = await axios.get(url, {
        headers: {
          "Content-Length": 0,
          "Content-Type": "text/plain",
        },
        responseType: "text",
      });
      //console.log('data', data);
      setAgreement({
        ...agreement,
        loading: true,
        text: data,
        header,
      });
    }

    if (!isLocal) {
      const { data } = await httpClient.get(
        api.url("GetAppLicenseAgreementText"),
        {
          platform: "web",
        },
        { "app-—Åode": config.APP_CODE }
      );
      //console.log('GetAppLicenseAgreementText');
      setAgreement({
        ...agreement,
        loading: true,
        text: data.text,
        header,
      });
    }
  } catch (err) {
    console.log("Err GetAppLicenseAgreementText ", getError(err));
    setAgreement({
      ...agreement,
      loading: true,
      error: getError(err),
    });
  }
}

export async function singUpConfirmationCode(
  dispatch,
  values,
  lang,
  isAnonymousChildrenEnabled,
  doCreateVisitAndLogin = false
) {
  const body = {
    login: values.login,
    password: values.password,
    confirmationCode: values.confirmationCode,
  };

  dispatch({ type: "LOADING_WITH_OLD_SERVER_RESPONSE" });
  try {
    const { data } = await httpClient.post(api.url("ConfirmNewAccount"), body, {
      config: { isWrap: false },
    });

    if (data != null && data.authToken) {
      saveAuthToken(data);

      //console.log('== === ConfirmNewAccount == === ', data);
      if (doCreateVisitAndLogin) {
        dispatch({
          type: "SET_SERVER_RESPONSE",
          payload: { action: "MAY_LOGIN" },
        });
        return;
      }
      await loginUserData({
        dispatch,
        lang,
        role: data.role,
        isAnonymousChildrenEnabled,
      });
    } else {
      dispatch({
        type: "SET_SERVER_RESPONSE",
        payload: { action: "WRONG_RESPONSE_IS_EMPTY" },
      });
    }
  } catch (err) {
    //console.log('ERROR singUpConfirmationCode ==', err, '');
    if (getError(err, "code") === "WRONG_CONFIRMATION_CODE")
      dispatch({
        type: "SET_SERVER_RESPONSE",
        payload: { action: "WRONG_CONFIRMATION_CODE" },
      });
    else
      dispatch({
        type: "SET_SERVER_RESPONSE",
        payload: { action: getError(err, "code"), text: getError(err) },
      });
  }
}

