export async function createUser(
  dispatch,
  values,
  countryCode = "RU",
  isCaptchaEnabled,
  setCaptchaToken,
  captchaToken
) {
  dispatch({ type: "LOADING" });
  const login = {};
  if (isValidEmail(values.login)) login.email = values.login;
  else if (isValidPhone(values.login, countryCode))
    login.phone = cleanPhoneValue(values.login);

  const body = {
    ...login,
    password: values.password,
    lastName: values.lastName,
    firstName: values.firstName,
    middleName: values.middleName,
    birthDate: values.birthDate,
    gender: values.gender,
    registrationSource: "web",
  };
  if (isCaptchaEnabled) {
    sessionStorage.setItem("captchaToken", captchaToken);
  }

  await httpClient
    .post(api.url("RegisterNewUser"), body, {
      config: { isWrap: false },
    })
    .then(({ data }) => {
      if (!isEmpty(data)) {
        dispatch({
          type: "SET_SERVER_RESPONSE",
          payload: {
            ...data,
            login: values.login,
            password: values.password,
            action: "CONFIRM_CODE_SENT",
          },
        });
      } else {
        dispatch({
          type: "SET_SERVER_RESPONSE",
          payload: { action: "WRONG_RESPONSE_REG_USER_IS_EMPTY" },
        });
      }
      if (isCaptchaEnabled) {
        setCaptchaToken(null);
        sessionStorage.removeItem("captchaToken");
      }
    })
    .catch((err) => {
      dispatch({
        type: "SET_SERVER_RESPONSE",
        payload: { action: getError(err) },
      });
    });
}

