export async function loginUserData({
  dispatch,
  lang,
  role = accountRole.patient,
  isAnonymousChildrenEnabled = false,
  mmkId = "parent",
}) {
  const clinicId = 0;

  const requests = [
    {
      name: "GetUserInfo",
      body: {
        mmkId,
        clinicId,
        withPhoto: true,
      },
    },
    { name: "GetDefaultMmkId", body: {} },
  ];

  const totalRequest = requests.map((req) =>
    httpClient.get(api.url(req.name), req.body)
  );

  try {
    const res = await axios.all(totalRequest);

    const fio = `${lastName(
      isAnonymousChildrenEnabled,
      res[0].data?.lastName
    )} ${res[0].data?.firstName ?? ""} ${res[0].data?.middleName ?? ""}`;

    const mmkLinkedList = [
      { clinicId, number: "parent", name: fio, photo: res[0].data?.photo },
    ];

    const authToken = localStorage.getItem("authToken");
    const phoneParent = res[0].data?.phone;
    const defaultMmkId = res[1]?.data?.defaultMmkId ?? mmkId;

    const userData = {
      user: !isEmpty(res[0].data)
        ? {
            ...res[0].data,
            mmkId,
            clinicId,
            firstName: firstName(
              isAnonymousChildrenEnabled,
              res[0].data?.firstName
            ),
            middleName: res[0].data?.middleName ?? "",
            lastName: lastName(
              isAnonymousChildrenEnabled,
              res[0].data?.lastName
            ),
            isAnonymous: res[0].data?.canDeleteAccount,
            role,
            lang,
            phoneParent,
          }
        : { lang, role },

      mmkLinkedList,
      authToken,
      defaultMmkId,
    };

    localStorage.setItem("user", JSON.stringify(userData.user));
    localStorage.setItem("mmkLinkedList", JSON.stringify(mmkLinkedList));
    localStorage.setItem("defaultMmkId", defaultMmkId);

    dispatch({
      type: "LOGIN_PATIENT",
      payload: userData,
    });
  } catch (err) {
    console.log("==== error loginUserData ==== \n", getError(err));
    dispatch({
      type: "SET_SERVER_RESPONSE",
      payload: getError(err),
    });
  }
}

export async function otpLogin({
  dispatch,
  lang,
  login,
  code = undefined,
  isAnonymousChildrenEnabled = false,
}) {
  const body = {
    login,
    code,
  };

  dispatch({ type: "LOADING" });

  try {
    const { data } = await httpClient.post(api.url("OtpLogin"), body);

    if (!isEmpty(data.confirmationCodeInfo)) {
      dispatch({
        type: "SET_SERVER_RESPONSE",
        payload: {
          ...data.confirmationCodeInfo,
          action: "CONFIRM_CODE_SENT",
        },
      });
    } else if (data.authToken != null) {
      saveAuthToken(data);
      dispatch({
        type: "SET_SERVER_RESPONSE",
        payload: {
          ...data,
          action: "OK",
        },
      });

      await loginUserData({
        dispatch,
        lang,
        role: data?.role,
        isAnonymousChildrenEnabled,
      });
    }
    // if (!isEmpty(data))

    //userDispatch(data);
  } catch (err) {
    console.log("Error ExternalLogin --- \n", getError(err));
    if (err?.response?.data?.code === "ACCOUNT_IS_NOT_CONFIRMED") {
      requestNewAccountConfirmationCode(dispatch, {
        login,
      });
      return;
    }

    dispatch({
      type: "SET_SERVER_RESPONSE",
      payload: {
        action: getError(err, "code"),
        error: getError(err),
      },
    });
    return;
  }
}
export async function loginUser(
  dispatch,
  emailOrPhone,
  password,
  lang,
  isAnonymousChildrenEnabled = false
) {
  dispatch({ type: "LOADING" });

  const login = {};
  login["login"] = emailOrPhone;

  await httpClient
    .post(api.url("ExternalLogin"), {
      ...login,
      password,
    })
    .then(({ data }) => {
      saveAuthToken(data);
      if (data?.isPasswordExpired) {
        dispatch({
          type: "SET_SERVER_RESPONSE",
          payload: {
            action: "EXP_PASS",
            login: emailOrPhone,
            text: i18next.t("PROFILE.EXPIRED_PASSWORD"),
          },
        });

        return;
      }
      if (data?.isTwoFactorAuth) {
        dispatch({
          type: "SET_SERVER_RESPONSE",
          payload: {
            action: "CONFIRM_CODE_SENT",
            login: emailOrPhone,

            ...data.confirmationCodeInfo,
          },
        });
        return;
      }
      //console.log(' ExternalLogin --- \n', data);
      return data;
    })
    .then(async (data) => {
      if (!isEmpty(data))
        await loginUserData({
          dispatch,
          lang,
          role: data?.role,
          isAnonymousChildrenEnabled,
        });
    })
    .catch((err) => {
      console.log("Error ExternalLogin --- \n", getError(err, "code"));
      if (getError(err, "code") === "ACCOUNT_IS_NOT_CONFIRMED") {
        requestNewAccountConfirmationCode(dispatch, {
          login: emailOrPhone,
          password,
        });
        return;
      }
      dispatch({
        type: "SET_SERVER_RESPONSE",
        payload: getError(err),
      });
    });
}

